<template>
    <div class="container">
        <div id='map'></div>
        <div id="categories">
            <!-- <div v-for="(item, category) in data" :key="category"> -->
            <!-- <input type="checkbox" :id="category" :name="category" v-model="selectedCategories" :value="category" />
                <label :for="category">
                    {{ category }} ({{ item.length }})
                </label> -->

            <el-checkbox-group v-model="selectedCategories">
                <el-checkbox style="margin-bottom: 10px;font-weight: bolder;font-size: 16px!important;"
                    v-for="(item, category) in data" :label="category" :key="category">{{ category }}({{ item.length }})
                </el-checkbox>
            </el-checkbox-group>
            <!-- </div> -->
        </div>
        <el-dialog :title="currentInfo.name + '简介'" :visible.sync="dialogVisible" width="calc(100% - 100px)" >
            <span>{{  currentInfo.description}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import data from './json.json'
import { GPS } from './LngLatTranlate';
import shadow from "./marker-shadow.png"
export default {
    components: {},
    data() {
        return {
            currentInfo:{
                name:"",
                description:""
            },
            dialogVisible:false,
            shadowUrl: shadow,
            map: null,
            data: data,
            selectedCategories: [],
            markers: [],
            colors: ['#409EFF', '#67C23A', '#E6A23C', '#F56C6C', '#f172a1', '#802bb1', '#564f6f', '#14A098']
        }
    },
    // 监听属性 类似于data概念
    computed: {
        // 计算属性用于生成动态样式
        popupStyles() {
            let styles = '';
            this.colors.forEach((color, index) => {
                styles += `
          .my-popup.my-${color.substring(1, 7)} .leaflet-popup-content-wrapper {
            border: 2px solid ${color};
          }
          .my-popup.my-${color.substring(1, 7)} .leaflet-popup-tip {
            border-right: 2px solid ${color};
            border-bottom: 2px solid ${color};
          }
        `;
            });
            return styles;
        },
    },
    // 监控data中的数据变化
    watch: {
        selectedCategories: {
            handler() {
                this.addMarkersToMap(); // 当选择的类别变化时，更新地图上的标记
            },
            deep: true,
        },
    },
    created() {
    },
    mounted() {
        this.initMap();
        // 动态插入样式
        const style = document.createElement('style');
        style.type = 'text/css';
        style.innerHTML = this.popupStyles;
        document.head.appendChild(style);
    },
    // 方法集合
    methods: {
        initMap() {

            //投影参数
            var crs = new L.Proj.CRS('EPSG:900913',
                '+proj=merc +a=6378206 +b=6356584.314245179 +lat_ts=0.0 +lon_0=0.0 +x_0=0 +y_0=0 +k=1.0 +units=m +nadgrids=@null +wktext  +no_defs',
                {
                    resolutions: [
                        262144,
                        131072,
                        65536,
                        32768,
                        16384,
                        8192,
                        4096,
                        2048,
                        1024,
                        512,
                        256,
                        128,
                        64,
                        32,
                        16,
                        8,
                        4,
                        2,
                        1,
                        0.5
                    ],
                    origin: [0, 0],
                    // bounds:L.bounds([20037508.342789244, 0], [0, 20037508.342789244])
                });

            this.map = L.map('map', {
                center: [19.645656, -155.51124154],
                crs,
                zoom: 11,
            })
            L.tileLayer('http://maponline0.bdimg.com/tile/?qt=vtile&x={x}&y={y}&z={z}&styles=pl&scaler=1&udt=20241010&from=jsapi3_0', {
                maxZoom: 19,
                tms: true,
                attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            }).addTo(this.map);


            this.map.on("click", function (e) {
                console.log(e.latlng);
            })
        },
        test(e) {
            console.log(e);
        },
        addMarkersToMap() {
            let _this = this;
            // 清除旧的标记
            this.markers.forEach(marker => marker.remove());
            this.markers = [];

            // 根据 selectedCategories 添加新的标记
            this.selectedCategories.forEach((category) => {
                const color = this.colors[Object.keys(this.data).indexOf(category)];
                this.data[category].forEach((item, index) => {
                    const html = `<div style="    margin-top: -49px;    position: relative;">
                        <i class=" iconfont kr-zuobiaodian-copy-copy-copy-copy-copy" style="color:${color};font-size:42px;margin-left: -14px;"></i>
                        <span style="position: absolute;top:17px;left: 3px; color: #fff;font-size: 14px;">${index + 1}</span>
                        </div>`

                    // WGS-84 to GCJ-02 to BD-09
                    const GCJ02 = GPS.gcj_encrypt(item.lat, item.lng);
                    const bd = GPS.bd_encrypt(GCJ02.lat, GCJ02.lon);
                    const icon = L.divIcon({
                        html,
                        shadowUrl: this.shadowUrl,
                    })

                    const popup = `<div style="min-width:160px">
                        <h4>${item.name}</h4>
                        <p>简介 ： ${item.description.substring(1, 100)}...   <a class="el-link el-link--primary is-underline" ><span class="el-link--inner"  style="font-size:12px">阅读更多</span></a></p>
                        <p><span style="display:inline-block;width:10px;height:10px;border-radius:50%; margin-right: 10px;
                                    border: 1px solid #fff;
                                    box-shadow: 0 0 14px #000;background:${color}"></span>${item.category}</p>
                        <p>lat:${item.lat}</p>
                        <p>lng:${item.lng}</p>
                        </div>`
                    const marker = L.marker([bd.lat, bd.lon], {
                        icon
                    })

                    // 添加事件监听器到 popup
                    marker.on('popupopen', function (e) {
                        const popupElement = e.popup._container;
                        const readMoreLink = popupElement.querySelector('.el-link--primary');

                        if (readMoreLink) {
                            readMoreLink.onclick =  () =>{
                                console.log(item.name)
                                _this.currentInfo = item
                                _this.dialogVisible = true;
                            }

                        }
                    })

                    // WGS-84
                    // const marker = L.marker([item.lat, item.lng])
                    marker.bindPopup(popup, { className: `my-popup  my-${color.substring(1, 7)}`, offset: [0, -42], autoClose: false })
                        .addTo(this.map).openPopup();


                    this.markers.push(marker);
                });
            });

            // eslint-disable-next-line new-cap
            const group = new L.featureGroup(this.markers);
            this.map.fitBounds(group.getBounds(), { maxZoom: 12 });
        },
    },
}
</script>
<style lang='scss' scoped>
.container {
    position: relative;

    #map {
        width: 100vw;
        height: 100vh;
    }

    #categories {
        position: absolute;
        bottom: 200px;
        width: 100px;
        left: 20px;
        z-index: 999;
    }


}
</style>

<style>
.leaflet-popup-content {
    margin: 14px;
}

.my-popup .leaflet-popup-content-wrapper {
    border: 1px solid red;
}

.my-popup .leaflet-popup-tip {
    box-shadow: none;
    padding: 0;
    border-right: 1px solid red;
    border-bottom: 1px solid red;
}

.leaflet-popup-content p {
    margin: 7px 0;
}

.leaflet-popup-content h4 {
    margin: 7px 0;
}

.leaflet-popup-tip-container {
    overflow: visible;
}
</style>

